import React from 'react';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import IncomingOrder from './OrderCards/IncomingOrder';
// import ScrollArea from 'react-scrollbar';
import 'react-perfect-scrollbar/dist/css/styles.css';
import PerfectScrollbar from 'react-perfect-scrollbar'


const useStyles = makeStyles(theme => ({
    Board: {
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        backgroundColor: '#424242',
        fontStyle: 'italic',
        fontWeight: 500,
        borderWidth: 5,
        borderColor: '#d6d7da',
    },
    orderContainer: {
        padding: theme.spacing(1),
        color: theme.palette.text.secondary,
        fontStyle: 'italic',
        fontWeight: 500,
        borderWidth: 5,
        borderColor: '#d6d7da',
    },
    h6: {
        color: '#FF6C00',
        fontWeight: 500
    }
}));

export default function IncomingBoard() {
    const classes = useStyles();

    return (
        <Grid className={classes.Board} item xs={7} >
            <Grid item >
                <Paper className={classes.paper} elevation={0} square={true}>
                    <h6 className={classes.h6}>Incoming Orders</h6>
                </Paper>
            </Grid>
            <PerfectScrollbar >

                <Grid container className={classes.orderContainer} spacing={1}>
                    <IncomingOrder />
                    <IncomingOrder />
                </Grid>

            </PerfectScrollbar>


        </Grid>
    );
}